import React, { memo } from 'react'
import clx from 'classnames'
import styles from './index.module.scss'

const GenerateField = memo(({ desc, className, children }) => {
  return (
    <div className={clx(
      styles.fieldArea,
      { [className]: className }
    )}>
      {desc && <label className={styles.fieldDesc}>{desc}</label>}
      {children}
    </div>
  )
})

export default GenerateField
