import React, { Component } from 'react'
import { connect } from 'react-redux'
import {
  change,
  reset
} from 'redux-form'
import ModifyOrder from '../page/ModifyOrder'
import {
  asyncSearchOrders,
  asyncCreateOrder,
  asyncUpdateOrder,
  asyncDeleteOrder
} from '../store/actions'

class ModifyOrderContainer extends Component {
  render () {
    return <ModifyOrder {...this.props} />
  }
}

const mapStateToProps = state => {
  return {
    isLoading: state.common.isLoading,
    currentOrders: state.order.currentOrders
  }
}

const mapDispatchToProps = dispatch => {
  return {
    actions: {
      searchOrders: order => dispatch(asyncSearchOrders(order)),
      createOrder: order => dispatch(asyncCreateOrder(order)),
      updateOrder: order => dispatch(asyncUpdateOrder(order)),
      deleteOrder: order => dispatch(asyncDeleteOrder(order)),
      updateForm: (form, field, value) => dispatch(change(form, field, value)),
      resetForm: form => dispatch(reset(form))
    }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ModifyOrderContainer)
