import {
  GET_ORDERS,
  SEARCH_ORDERS,
  CREATE_ORDER,
  UPDATE_ORDER,
  DELETE_ORDER
} from './actionTypes'
import { setLoading } from './commonActions'

const sleep = (ms = 1500) => new Promise(resolve => setTimeout(resolve, ms))

const asyncAction = action => payload => {
  return async (dispatch, getState) => {
    dispatch(setLoading(true))
    await sleep(1000)
    dispatch(action(payload))
    dispatch(setLoading(false))
  }
}

export const getOrders = () => {
  return {
    type: GET_ORDERS,
    payload: {}
  }
}

export const searchOrders = (order) => {
  return {
    type: SEARCH_ORDERS,
    payload: {
      order
    }
  }
}

export const createOrder = order => {
  return {
    type: CREATE_ORDER,
    payload: {
      order
    }
  }
}

export const updateOrder = order => {
  return {
    type: UPDATE_ORDER,
    payload: {
      order
    }
  }
}

export const deleteOrder = order => {
  return {
    type: DELETE_ORDER,
    payload: {
      order
    }
  }
}

export const asyncGetOrders = asyncAction(getOrders)
export const asyncSearchOrders = asyncAction(searchOrders)
export const asyncCreateOrder = asyncAction(createOrder)
export const asyncUpdateOrder = asyncAction(updateOrder)
export const asyncDeleteOrder = asyncAction(deleteOrder)
