import React from 'react'
import { Field, reduxForm } from 'redux-form'
import clx from 'classnames'
import {
  DRINKS,
  ORDER_FORM,
  MODIFY,
  MODIFY_MAP,
  FORM
} from '../../constants'
import GenerateField from '../GenerateField'
import styles from './index.module.scss'

// only allow \n \r \t
const nonPrintCharRegexp = new RegExp(`[\x00-\x09\x0B-\x0C\x0E-\x1F\x7F-\x9F]`, 'g')
const removeNonPrintChar = value => value.replace(nonPrintCharRegexp, '')

class OrderForm extends React.PureComponent {
  render () {
    const {
      handleSubmit,
      pristine,
      reset,
      submitting,
      modifyType,
      isLoading
    } = this.props
    const isDelete = modifyType === MODIFY.DELETE
    const isDisabledSubmit = (
      submitting ||
      (!isDelete && pristine)
    )
    return (
      <form
        className={clx({ hidden: isLoading })}
        onSubmit={handleSubmit}
      >
        <GenerateField desc='Drink'>
          <Field
            className={clx(styles.field, styles.selectField)}
            component='select'
            name={ORDER_FORM.DRINK_KEY}
            disabled={isDelete}
            required
          >
            <option value=''>Please choose your drink</option>
            {
              DRINKS.map(({ key, name, price }) => (
                <option key={key} value={key}>{name} - $ {price}</option>
              ))
            }
          </Field>
        </GenerateField>
        <GenerateField desc='Notes'>
          <Field
            className={clx(styles.field, styles.textareaField)}
            component='textarea'
            name={ORDER_FORM.NOTES}
            parse={removeNonPrintChar}
            placeholder='Please enter anything you need'
            disabled={isDelete}
          />
        </GenerateField>
        <GenerateField className={clx({ hidden: modifyType !== MODIFY.CREATE })}>
          <Field
            id={ORDER_FORM.KEEP_ACTION}
            name={ORDER_FORM.KEEP_ACTION}
            component='input'
            type='checkbox'
          />
          &nbsp;
          <label htmlFor={ORDER_FORM.KEEP_ACTION}>Keep action</label>
        </GenerateField>
        <div className={styles.actionArea}>
          <button
            type='submit'
            className={styles.submitBtn}
            disabled={isDisabledSubmit}
          >
            {MODIFY_MAP[modifyType]}
          </button>
          <button
            type='button'
            className={clx(
              styles.resetBtn,
              { hidden: isDelete }
            )}
            disabled={isDisabledSubmit}
            onClick={reset}
          >
            Reset
          </button>
        </div>
      </form>
    )
  }
}

const fields = [
  ORDER_FORM.DRINK_KEY,
  ORDER_FORM.NOTES,
  ORDER_FORM.KEEP_ACTION
]

export default reduxForm({
  fields,
  form: FORM.ORDER_FORM
})(OrderForm)
