import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import _ from 'lodash'
import {
  MODIFY,
  MODIFY_MAP,
  FORM,
  ORDER_FORM
} from '../../constants'
// import styles from './index.module.scss'
import PageTitle from '../../component/PageTitle'
import OrderForm from '../../component/OrderForm'
import Loader from '../../component/Loader'

class ModifyOrder extends Component {
  componentDidMount () {
    const { modifyType, match } = this.props
    if (modifyType !== MODIFY.CREATE) {
      const orderId = _.get(match, 'params.orderId', '')
      this.getCurrentOrder(orderId)
    }
  }

  shouldComponentUpdate (nextProps) {
    if (nextProps.modifyType !== this.props.modifyType) {
      return true
    }
    return false
  }

  getCurrentOrder = async (id) => {
    const { actions } = this.props
    const { searchOrders, updateForm } = actions
    await searchOrders({ id })
    const { currentOrders } = this.props
    const { key: drinkKey, notes } = _.get(currentOrders, '0', {})
    updateForm(FORM.ORDER_FORM, ORDER_FORM.DRINK_KEY, drinkKey)
    updateForm(FORM.ORDER_FORM, ORDER_FORM.NOTES, notes)
  }

  onOrderSubmit = async (order) => {
    const { actions, modifyType, match, history } = this.props
    const {
      createOrder,
      updateOrder,
      deleteOrder,
      resetForm,
      updateForm
    } = actions
    const orderId = _.get(match, 'params.orderId', '')
    let action = Promise.resolve()
    switch (modifyType) {
      case MODIFY.CREATE: action = createOrder(order)
        break
      case MODIFY.UPDATE: action = updateOrder({ ...order, id: orderId })
        break
      case MODIFY.DELETE: action = deleteOrder({ ...order, id: orderId })
        break
      default:
        break
    }
    await action
    if (order.keepAction) {
      resetForm(FORM.ORDER_FORM)
      updateForm(FORM.ORDER_FORM, ORDER_FORM.KEEP_ACTION, true)
    } else {
      history.push('/')
    }
  }
  render () {
    const { modifyType, isLoading } = this.props
    return (
      <div>
        <PageTitle>{MODIFY_MAP[modifyType]} Order</PageTitle>
        <Loader isLoading={isLoading} />
        <OrderForm
          isLoading={isLoading}
          onSubmit={this.onOrderSubmit}
          modifyType={modifyType}
        />
      </div>
    )
  }
}

export default withRouter(ModifyOrder)
